<template>
    <div class="box_main text-center col_w">
        <div class="skip" @click="closePage">{{clo}}</div>
        <h1 class="font_one"></h1>
        <div class="entrance" @click="loadPage"><span></span></div>
    <loading></loading>
    </div>
    
</template>

<style lang="css" scoped>
    @import '../assets/css/bootstrap.css';
    @import '../assets/css/introduction.css';
    @import '../assets/css/resets.css';
</style>
<script>
    import loading from './loading'
    import{getEnvData,getSOwTypeList_new} from '../api/getData'
    import { Indicator } from 'mint-ui';
    export default {
        name:'welcome',
        data(){
            return {
                "clo":"",
            }
        },
        components:{
            loading:loading,
        },
        props:[],
        methods:{
             closePage:function(){
                 alert("");
             },
             loadPage:function(){
                 this.$router.push('sowpig')
             },
            /**
             * 
             */
            async getEnv(){
                /*Indicator.open({
                    text: 'Loading...',
                    spinnerType: 'triple-bounce'
                });*/
                var envData = await getEnvData();
                //Indicator.close();
                console.log("" + JSON.stringify(envData));
            },

            /**
             *  
             */
            async getpigType(){
                
                var pigTypes = await getSOwTypeList_new();
                console.log("" + JSON.stringify(pigTypes));
            },

        },
        mounted:function(){
            this.getEnv();
            this.getpigType();
        },

    }

</script>



