/**
 * 
 * 
 * baseUrl: 
 * routerMode: 
 * imgBaseUrl: 
 * 
 */

let baseUrl = ''; 
let routerMode = 'history';
let imgBaseUrl = 'http://images.cangdu.org/';


//

if (process.env.NODE_ENV == 'development') {  
	 baseUrl = ""
}else if(process.env.NODE_ENV == 'production'){
	 baseUrl = 'http://uic.umu360.com/api/'
}

export {
	baseUrl,
	routerMode,
	imgBaseUrl,
}


