
/**
 * axios
 */
 import axios from 'axios'
 import {baseUrl} from './env'
 //get
 export default async(url = '', data = {}, type = 'GET'/*,succ,fail*/,extra) => {
     
    type = type.toUpperCase();
    url = baseUrl + url;

    if (type == 'GET') {
		let dataStr = ''; //
		Object.keys(data).forEach(key => {
			dataStr += key + '=' + data[key] + '&';
		})

		if (dataStr !== '') {
			dataStr = dataStr.substr(0, dataStr.lastIndexOf('&'));
			url = url + '?' + dataStr;
		}
    }
    
    if(axios){
       
        var requestConfig = {			
            method: type,
            url:url,
			headers: {
				'Accept': 'application/json',
				'Content-Type': 'application/json'
            },
        }
        console.log("axios" + url);
        if(extra){
            extra.url  = url
            extra.method = type
            extra.headers = requestConfig.headers
            requestConfig = extra
        }

        
        if (type == 'POST') {
			Object.defineProperty(requestConfig, 'data', {
				value: JSON.stringify(data)
			})
        }
        
        try {
			/*await axios(requestConfig).then(function(response){
                console.log(response);
                if(succ){
                    succ(response);
                }
                return response
            })
            .catch(function(error){
                if(error.response){
                    //
                    //2xx
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                }else{
                    //
                    console.log('Error',error.message);
                }
                if(fail){
                    fail(error);
                }
                console.log(error.config);
            }); */


            // 
            /*axios.interceptors.response.use(function(response){
                console.log(1);
                let data = response.data;
                let status = response.status;
                if(status===200 || status === 304){
                    console.log(2);
                    let rstno = data.rstno;
                    if(rstno>0){
                        console.log(3);
                        return data;
                    }
                    console.log(4);
                    return Promise.reject(response);
                }else{
                    if(status===412){
                        console.log(5);
                        instance.$store.commit('doLogout')
                        instance.$router.push({
                            path:'/login',
                            query:{'redirect':instance.$route.fullPath},
                        })
                    }
                    console.log(6);
                    return Promise.reject(response);
                }
            })*/



            return new Promise((resolve, reject) => {
               
                axios(requestConfig)
                    .then(response => {
                        resolve(response.data);
                    })
                    .catch((error) => {
                         alert(error);
                        if (error.response) {
                            // The request was made and the server responded with a status code
                            // that falls out of the range of 2xx
                            console.log(error.response.data);
                            console.log(error.response.status);
                            console.log(error.response.headers);
                        } else if (error.request) {
                            // The request was made but no response was received
                            // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                            // http.ClientRequest in node.js
                            console.log(error.request);
                        } else {
                            // Something happened in setting up the request that triggered an Error
                            console.log('Error', error.message);
                        }
                        console.log(error.config);
                    })
            })


		} catch (error) {
			throw new Error(error)
		}

    }

 }

